 @import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;800&display=swap");

 *{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat',sans-seriff;
  background-color: #1a1a1c;
  color: white;
 }

 .coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top:64px ;
 }

 .coin-scearch{
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
 }

 .coin-text{
  margin-bottom: 32px;
  text-align: center;
 }

 .coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 6px;
  border: none !important;
  background-image: linear-gradient(-225deg,#ac32e4 0%, #7918f2 48%,#4801ff 100%);
  color: #e2e2e2;
 }

 .coint-input::placeholder{
  color: #e2e2e2;
 }
