.coin-container{
    display: flex;
    justify-content: center;
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-items:start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}
.coin{
    display:flex ;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1{
    font-size: 16px;
    width: 150px;
    margin-right: 10px;
}

.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol{
    text-transform: uppercase;
}

.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100px;
}

.coin-price{
    width: 110px;
    padding-right: 4rem;
}

.coin-volume{
    width: 200px;
    padding-right: 4rem;
}

.coin-marketcap{
    width: 240px;
    margin-right: 0;
}

.coin-percent{
    width: 80px;
}

.red{
    color: #f00606;
    padding-right: 4rem;
}

.green{
    color: #11d811;
    padding-right: 4rem;
}
